import { createApp } from 'vue'
import { Layout, Button, Select, Checkbox, Divider, Table, Tabs, TabPane, Tag } from 'ant-design-vue';
import App from './App.vue'
import store from './store'
import "ant-design-vue/dist/antd.css";

const app = createApp(App)

app.use(store)
app.use(Checkbox)
app.use(Layout)
app.use(Button)
app.use(Select)
app.use(Divider)
app.use(Table)
app.use(Tabs)
app.use(TabPane)
app.use(Tag)

app.mount("#app")

